@charset "utf-8";
* {                  /* 清除不同浏览器的不同外边距 */
	margin: 0;
	padding: 0;
}
/* 除去浮动*/
.clear {
	clear: both;
}
/* 水平居中 */
.center {
	margin-left: auto;
	margin-right: auto;
}
.tableDefault {
	border: 1px solid #F5EDFB;
	padding:0px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 5px;
	border-collapse:collapse;
	table-layout:fixed;
}
td{
	margin:0;
	padding:2px;	
}
.trDefault {
	border: 1px solid #F5EDFB;
}
.tdDefault{
	height-line:18px;
	text-overflow:ellipsis;/*当文本溢出时显示…此时还必须定义：
	强制文本在一行内显示（white-space:nowrap）及溢出内容为隐藏（overflow:hidden）*/
	white-space:nowrap;/*表示文本不会换行*/
	word-break:keep-all;/* 不换行 */
	overflow:hidden;/*表示溢出的内容会被隐藏*/
}
tr:nth-child(odd){
	background-color: #FBF9FD;	
}
/*取消超链接的下划线*/
a:link {
	text-decoration: none;
	color: #000000; /*连接默认的颜色变化*/ 
}
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: underline;
	color: #ff0000; /*鼠标经过的颜色变化*/
}
a:active {
	text-decoration: none;
	color: #00BFFF; /*鼠标按下的颜色变化*/
}
.model {
	height: 100%;
	width: 100%;
